import React from "react";
import { useEffect, useState } from "react";

import { useCookies } from "react-cookie";
import { useNavigate } from "react-router-dom";
import { useSelector } from "react-redux";
import {
  selectLoginPgState,
  selectSignUpPgState,
} from "./../app/reducer/userSlice";
import "./../styles/Login.css";
import SignInForm from "./user/SignInForm";
import SignUpForm from "./user/SignUpForm";

const Login = () => {
  const navigate = useNavigate();

  const [cookies, setCookie, removeCookie] = useCookies(["userData"]);

  const isLoginPg = useSelector(selectLoginPgState);
  const isSignUpPg = useSelector(selectSignUpPgState);
  //    props 
  const [signInData, setSignInData] = useState({
    email: "",
    password: "",
  });

  //   props
  const [signUpData, setSignUpData] = useState({
    email: "",
    password: "",
    rePassword: "",
    name: "",
  });

  const onChangeSignInData = (e) => {
    setSignInData({
      ...signInData,
      [e.target.name]: e.target.value,
    });
  };

  const onChangeSignUpData = (e) => {
    setSignUpData({
      ...signUpData,
      [e.target.name]: e.target.value,
    });
  };

  //   ,/diary/home 
  useEffect(() => {
    if (cookies.userData === undefined) {
      console.log(cookies.userData);
      navigate("/");
    } else {
      console.log(cookies.userData);
      navigate("/diary/home");
    }
  }, [cookies]);
  return (
    <div className="login__main">
      {isLoginPg === true ? (
        <SignInForm
          signInData={signInData}
          setSignInData={setSignInData}
          onChangeSignInData={onChangeSignInData}
        />
      ) : isSignUpPg === true ? (
        <SignUpForm
          signUpData={signUpData}
          setSignUpData={setSignUpData}
          onChangeSignUpData={onChangeSignUpData}
        />
      ) : (
        <></>
      )}
    </div>
  );
};

export default Login;
